import PropTypes, { instanceOf } from 'prop-types';
import * as Yup from 'yup';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { useCallback, useEffect, useMemo, useState } from 'react';
// form
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui

import { styled } from '@mui/material/styles';
import { LoadingButton } from '@mui/lab';
import { Card, Chip, Grid, Stack, TextField, Typography, Autocomplete, InputAdornment, Box } from '@mui/material';
// routes
import { PATH_APPLICATION, PATH_DASHBOARD } from '../../../../routes/paths';
// components
import {
  FormProvider,
  RHFSwitch,
  RHFSelect,
  RHFEditor,
  RHFTextField,
  RHFRadioGroup,
  RHFUploadMultiFile,
  RHFUploadSingleFile
} from '../../../../components/hook-form';
// redux
import { useDispatch, useSelector } from '../../../../redux/store';
import axios from '../../../../utils/axios';
import UploadServerProgress from '../../../../pages/UploadServerProgress';
// ----------------------------------------------------------------------
const GENDER_OPTION = ['men', 'women', 'kids'];

const LabelStyle = styled(Typography)(({ theme }) => ({
  ...theme.typography.subtitle2,
  color: theme.palette.text.secondary,
  marginBottom: theme.spacing(1),
}));

// ----------------------------------------------------------------------

ProductNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentProduct: PropTypes.object,
};

export default function ProductNewForm({ isEdit, currentProduct }) {
  const { categories, userCategories, tags, sizes } = useSelector((state) => state.category);
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const FILE_MAX_SIZE = 1024 * 1024 * 10;
  const SUPPORTED_FORMATS = ['image/jpeg', 'image/jpg', 'image/png']
  const NewProductSchema = Yup.object().shape({
    name: Yup.string().required('Name is required'),
    description: Yup.string().required('Description is required'),
    subImages: Yup.array().min(1, 'Sub images is required'),
    coverImage: Yup.mixed().test('fileSize', 'No file selected or file too large', (value) => value === null || (value && value.size < FILE_MAX_SIZE)).test('fileFormat', 'Unsupported file type', (value) => value === null || (value && value && SUPPORTED_FORMATS.includes(value.type))),
    salePrice: Yup.number().moreThan(0, 'Price should not be $0.00'),
    price: Yup.number().moreThan(0, 'Price should not be $0.00'),
  });
  const EditProductSchema = Yup.object().shape({
    salePrice: Yup.number().moreThan(0, 'Price should not be $0.00'),
    price: Yup.number().moreThan(0, 'Price should not be $0.00'),
  });
  const defaultValues = useMemo(
    () => ({
      video: currentProduct?.video || '',
      name: currentProduct?.name || '',
      description: currentProduct?.description || '',
      coverImage: currentProduct?.coverImage || '',
      subImages: currentProduct?.subImages || [],
      code: currentProduct?.code || '',
      sku: currentProduct?.sku || '',
      price: currentProduct?.price || 0,
      priceSale: currentProduct?.priceSale || 0,
      tags: currentProduct?.tags || [],
      inStock: (currentProduct?.inStock || true),
      taxes: (currentProduct?.taxes || true),
      gender: currentProduct?.gender || GENDER_OPTION[2],
      category: currentProduct?.category || '',
      weight: currentProduct?.weight || 0,
      sizes: currentProduct?.sizes || [],
      colors: currentProduct?.colors || true,
      userCategory: currentProduct?.userCategory || '',
      state: (currentProduct?.state === 'active' || true)
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentProduct]
  );
  const methods = useForm({
    resolver: yupResolver(!isEdit ? NewProductSchema : EditProductSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    control,
    setValue,
    getValues,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  const values = watch();
  const [loading, setLoading] = useState(false);
  const onSubmit = async () => {
    try {
      setLoading(true);
      const formData1 = new FormData();
      const formData2 = new FormData();
      const formData3 = new FormData();
      let initVideo = false;
      let initCover = false;
      let initImages = false;
      if (values.coverImage instanceof Object) {
        formData2.append('coverImage', values.coverImage);
        initCover = true;
      }

      if (values.video instanceof Object) {
        formData1.append('video', values.video);
        initVideo = true;
      }

      if (values.subImages[0] instanceof Object) {
        values.subImages.forEach(e => {
          formData3.append('subImages', e);
        });
        initImages = true;
      }

      formData3.append('subImages', values.subImages);
      values.video = "";
      values.coverImage = "";
      values.subImages = [];
      if (isEdit)
        values._id = currentProduct._id;
      let response = await axios.post('/api/product/upload', values);
      const { product } = response.data;
      if (product) {
        if (initCover) {
          formData2.append("product", product);

          response = await axios.post('/api/product/upload/coverImage', formData2);
        }
        if (initImages) {
          formData3.append("product", product);
          response = await axios.post('/api/product/upload/subImages', formData3);
        }

      }
      // reset();
      setLoading(false);
      enqueueSnackbar(!isEdit ? 'Create progress is successed!' : 'Update progress is successed!');
      navigate(PATH_APPLICATION.profile.product);
    } catch (error) {
      console.error(error);
      setLoading(false);
    }

  };

  const handleCoverDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file)
        setValue(
          'coverImage',
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
    },
    [setValue]
  )
  const handleDrop = useCallback(
    (acceptedFiles) => {
      setValue(
        'subImages',
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );
    },
    [setValue]
  );

  const handleRemoveAll = () => {
    setValue('subImages', []);
  };

  const handleRemove = (file) => {
    const filteredItems = values.subImages?.filter((_file) => _file !== file);
    setValue('subImages', filteredItems);
  };

  const [CATEGORY_OPTION, SET_CATEGORY_OPTION] = useState([{ group: '', classify: [] }]);

  const categoryoption = [];
  useEffect(() => {
    let tco = { group: '' };
    categories.forEach((c, index) => {
      if (c.group !== tco.group) {
        tco = c;
        categoryoption.push({ group: c.group, classify: [{ value: c.classify, key: c.key }] });
      }
      else {
        categoryoption.forEach((co, index) => {
          if (co.group === c.group) {
            co.classify.push({ value: c.classify, key: c.key });
          }
        });
      }
    });
    if (categoryoption.length > 0)
      SET_CATEGORY_OPTION(categoryoption);
  }, []);
  return (
    <>
      {loading && <UploadServerProgress />}
      <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                <RHFTextField name="name" label="Product Name" />

                <div>
                  <LabelStyle>Description</LabelStyle>
                  <RHFEditor simple name="description" />
                </div>
              </Stack>

              <Stack spacing={3}>

                <div>
                  <LabelStyle>Description Video</LabelStyle>

                  <RHFUploadSingleFile
                    name="video"
                    showPreview
                    accept="video/*"
                    maxSize={3145728}
                    onDrop={handleCoverDrop}

                  />

                </div>
                <div>
                  <LabelStyle>Cover Image</LabelStyle>

                  <RHFUploadSingleFile
                    name="coverImage"
                    showPreview
                    accept="image/*"
                    maxSize={3145728}
                    onDrop={handleCoverDrop}

                  />

                </div>

                <div>
                  <LabelStyle>Sub Images</LabelStyle>
                  <RHFUploadMultiFile
                    name="subImages"
                    showPreview
                    accept="image/*"
                    maxSize={3145728}
                    onDrop={handleDrop}
                    onRemove={handleRemove}
                    onRemoveAll={handleRemoveAll}
                  />
                </div>
              </Stack>
            </Card>
          </Grid>

          <Grid item xs={12} md={6}>
            <Stack spacing={3}>

              <Card sx={{ p: 3 }}>
                <RHFSwitch name="inStock" label="In stock" />

                <Stack spacing={3} mt={2}>
                  <RHFTextField name="code" label="Product Code" />
                  <RHFTextField name="sku" label="Product SKU" />
                  <div>
                    <LabelStyle>Gender</LabelStyle>
                    <RHFRadioGroup
                      name="gender"
                      options={GENDER_OPTION}
                      sx={{
                        '& .MuiFormControlLabel-root': { mr: 4 },
                      }}
                    />
                  </div>

                  <RHFSelect name="category" label="HHU Category">

                    {CATEGORY_OPTION.length > 0 && CATEGORY_OPTION.map((category) => (
                      <optgroup key={category.group} label={category.group}>
                        {category.classify.map((classify) => (
                          <option key={classify.key} value={classify.key}>
                            {classify.value}
                          </option>
                        ))}
                      </optgroup>
                    ))}
                  </RHFSelect>

                  <RHFSelect name="userCategory" label="User Store Category">
                    <option key='' value=''>&nbsp;</option>
                    {userCategories.length > 0 && userCategories.map((category) => (
                      <option key={category.key} value={category.classify}>
                        {category.classify}
                      </option>
                    ))}
                  </RHFSelect>

                  <Controller
                    name="tags"
                    control={control}
                    render={({ field }) => (
                      <Autocomplete
                        {...field}
                        multiple
                        freeSolo
                        onChange={(event, newValue) => field.onChange(newValue)}
                        options={tags.map((option) => option.name)}
                        renderTags={(value, getTagProps) =>
                          value.map((option, index) => (

                            <Chip {...getTagProps({ index })} key={option} size="small" label={option} />
                          ))
                        }
                        renderInput={(params) => <TextField label="Tags" {...params} />}
                      />
                    )}
                  />
                </Stack>
              </Card>

              <Card sx={{ p: 3 }}>
                <Stack spacing={3} mb={2}>
                  <RHFTextField
                    name="price"
                    label="Regular Price"
                    placeholder="0.00"
                    value={getValues('price') === 0 ? '' : getValues('price')}
                    onChange={(event) => setValue('price', Number(event.target.value))}
                    InputLabelProps={{ shrink: true }}
                    InputProps={{
                      startAdornment: <InputAdornment position="start">$</InputAdornment>,
                      type: 'number',
                    }}
                  />

                  <RHFTextField
                    name="priceSale"
                    label="Sale Price"
                    placeholder="0.00"
                    value={getValues('priceSale') === 0 ? '' : getValues('priceSale')}
                    onChange={(event) => setValue('priceSale', Number(event.target.value))}
                    InputLabelProps={{ shrink: true }}
                    InputProps={{
                      startAdornment: <InputAdornment position="start">$</InputAdornment>,
                      type: 'number',
                    }}
                  />
                </Stack>

                <RHFSwitch name="taxes" label="Price includes taxes" />
              </Card>

              <Card sx={{ p: 3 }}>
                <Stack spacing={3} mb={2}>
                  <RHFSwitch name="state" label="Active this Product" />
                  <Controller
                    name="sizes"
                    control={control}
                    render={({ field }) => (
                      <Autocomplete
                        {...field}
                        multiple
                        freeSolo
                        onChange={(event, newValue) => field.onChange(newValue)}
                        options={sizes.map((option) => option.name)}
                        renderTags={(value, getTagProps) =>
                          value.map((option, index) => (
                            <Chip {...getTagProps({ index })} key={option} size="small" label={option} />
                          ))
                        }
                        renderInput={(params) => <TextField label="Size Of Product" {...params} />}
                      />
                    )}
                  />

                  <RHFTextField
                    name="weight"
                    label="Weight of Product"
                    placeholder="0.00"
                    value={getValues('weight') === 0 ? '' : getValues('weight')}
                    onChange={(event) => setValue('weight', Number(event.target.value))}
                    InputLabelProps={{ shrink: true }}
                    InputProps={{
                      startAdornment: <InputAdornment position="start">Kg</InputAdornment>,
                      type: 'number',
                    }}
                  />
                  <RHFSwitch name="colors" label="Product contains other colors" />

                </Stack>


              </Card>
              <LoadingButton type="submit" variant="contained" size="large" loading={loading}>
                {!isEdit ? 'Create Product' : 'Save Changes'}
              </LoadingButton>
            </Stack>
          </Grid>
        </Grid>
      </FormProvider>
    </>
  );
}
