import {dispatch, useSelector} from '../redux/store';
import {setHHUCategories,setSizes,setTags,setUserCategories} from '../redux/slices/category';
import {setAccountSuccess} from '../redux/slices/account';
import useAuth from '../hooks/useAuth';
import axios from './axios';
// -----------------------------------------

const initializeUserData = async(isAuthed, callback)=>{
  try{

    let response = await axios.get('/api/product/categories');
    const {categories} = response.data;
  
    dispatch(setHHUCategories(categories));
  
    response = await axios.get('/api/product/sizes');
    const { sizes} = response.data;
    dispatch(setSizes(sizes));
    response = await axios.get('/api/product/tags');
    const { tags} = response.data;
    dispatch(setTags(tags));
    
    if(isAuthed){
      response = await axios.post('/api/product/userCategories');
      const { userCategories} = response.data;
      dispatch(setUserCategories(userCategories));
      response = await axios.get('/api/account/general');
      const {account} = response.data;
      dispatch(setAccountSuccess(account));
    }
  
  }
  catch(err){
    console.log(err);
  }
  callback();
}
export default initializeUserData;